.big-button {
    background: $purple;
    border: none;
    border-bottom: 0.6rem solid darken($purple, 10%);
    color:white;
    font-weight: bold;
    font-size: $l-size;
    width: 100%;
    margin-bottom: $m-size;
    padding: 2.4rem;
}

.big-button:disabled {
    opacity: 0.5;
}

@media (min-width: $desktop-breakpoint) {
    .big-button {
        margin-bottom: $xl-size;
    }
}

.button {
    background-color: $purple;
    border: none;
    border-bottom: 0.3rem solid darken($purple, 10%);
    color:white;
    padding: $s-size;
}

.button--link {
    background: none;
    border:none;
    color: $off-white;
}
.danger {
    background-color: $danger;
    border-bottom: 0.3rem solid darken($danger, 10%);
}
.szh-menu-button {
    background-color: $off-black;
    border: none;
    border: 0.1rem solid $off-white;
    color:white;
    padding: $s-size;
}